
body {
  font-family: 'Montserrat', sans-serif;
  color: #fff;
}
a {
  text-decoration: none;
  color: #fff;
}
@-webkit-keyframes arrowAnimation {
0% {
}
25% {
    bottom: 30px;
}
50% {
    bottom: 20px;
}
100% {
    bottom: 30px;
}
}
@keyframes arrowAnimation {
0% {
}
25% {
    bottom: 30px;
}
50% {
    bottom: 20px;
}
100% {
    bottom: 30px;
}
}
.index {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  background-image: url("https://c.wallhere.com/photos/88/c5/dark_video_games-147593.png!d");
  background-size: cover;
  background-repeat: no-repeat;
}
.index .menu-container {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    width: 100%;
}
.index .menu-container .menu-item {
      height: 200px;
      width: 200px;
      text-align: center;
      position: relative;
      display: inline-block;
      -webkit-transition: height .5s;
      transition: height .5s;
      margin-left: 5px;
}
@media screen and (max-width: 768px) {
.index .menu-container .menu-item {
          height: 150px;
          width: 100%;
          margin-left: 0;
}
}
.index .menu-container .menu-item:hover {
        height: 250px;
}
@media screen and (max-width: 768px) {
.index .menu-container .menu-item:hover {
            height: 150px;
}
}
.index .menu-container .menu-item:hover .content-box .content {
          top: 50%;
}
.index .menu-container .menu-item:hover .content-box .content .description {
            opacity: 1;
}
.index .menu-container .menu-item:first-child {
        background-color: rgba(16, 78, 139, 0.3);
        height: 250px;
}
@media screen and (max-width: 768px) {
.index .menu-container .menu-item:first-child {
            height: 150px;
}
}
.index .menu-container .menu-item:first-child .content-box .content {
          top: 40%;
}
.index .menu-container .menu-item:first-child .content-box .content .description {
            opacity: 1;
}
.index .menu-container .menu-item:nth-child(n+2) {
        background-color: rgba(230, 57, 32, 0.3);
}
.index .menu-container .menu-item:nth-child(n+3) {
        background-color: rgba(252, 207, 64, 0.3);
}
.index .menu-container .menu-item .content-box {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
}
.index .menu-container .menu-item .content-box:after {
          content: '';
          position: absolute;
          bottom: 30px;
          width: 0;
          height: 0;
          border-style: solid;
          border-width: 8px 8px 0 8px;
          border-color: #fff transparent transparent transparent;
          left: 45%;
          -webkit-animation-name: arrowAnimation;
                  animation-name: arrowAnimation;
          -webkit-animation-duration: 2s;
                  animation-duration: 2s;
          -webkit-animation-iteration-count: infinite;
                  animation-iteration-count: infinite;
}
@media screen and (max-width: 768px) {
.index .menu-container .menu-item .content-box:after {
              content: none;
}
}
.index .menu-container .menu-item .content-box .content {
          position: relative;
          top: 60%;
          -webkit-transform: translateY(-50%);
                  transform: translateY(-50%);
          width: 100%;
          left: 0;
          -webkit-transition: top .5s;
          transition: top .5s;
}
@media screen and (max-width: 768px) {
.index .menu-container .menu-item .content-box .content {
              top: 50%;
}
}
.index .menu-container .menu-item .content-box .content .title {
            font-weight: bold;
}
.index .menu-container .menu-item .content-box .content .description {
            opacity: 0;
            -webkit-transition: opacity .5s;
            transition: opacity .5s;
}
@media screen and (max-width: 768px) {
.index .menu-container .menu-item .content-box .content .description {
                opacity: 1;
}
}
.about-me {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  background-image: url("https://c.wallhere.com/photos/88/c5/dark_video_games-147593.png!d");
  background-size: cover;
  background-repeat: no-repeat;
}
.about-me .content-container {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    text-align: center;
    left: 100px;
    right: 100px;
    margin: 100px 0;
    padding: 20px;
    overflow: auto;
    -webkit-transition: all .5s;
    transition: all .5s;
    background-color: rgba(16, 78, 139, 0.3);
}
.about-me .content-container::-webkit-scrollbar {
      width: 10px;
}
.about-me .content-container::-webkit-scrollbar-track {
      background: #f1f1f1;
}
.about-me .content-container::-webkit-scrollbar-thumb {
      background: #888;
}
.about-me .content-container::-webkit-scrollbar-thumb:hover {
      background: #555;
}
@media screen and (max-width: 768px) {
.about-me .content-container {
        left: 10px;
        right: 10px;
        margin: 50px 0 10px 0;
}
}
.about-me .content-container .title h1 {
      display: inline-block;
}
@media screen and (max-width: 768px) {
.about-me .content-container .title h1 {
          display: block;
}
}
.about-me .content-container .title img {
      width: 30px;
      height: 30px;
}
.about-me .content-container .stacks .stack {
      width: 20%;
      text-align: center;
      display: inline-block;
      vertical-align: top;
      margin-right: 10px;
      margin-bottom: 15px;
}
@media screen and (max-width: 768px) {
.about-me .content-container .stacks .stack {
          width: 25%;
}
}
.about-me .content-container .stacks .stack .icon {
        width: 50px;
        height: 50px;
        margin: 0 auto;
}
.about-me .content-container .stacks .stack .icon img {
          width: 100%;
}
